<template>
  <section>
    <template>
      <div class="contact-us relative">
        <img src="../assets/header-bg1.svg" alt="" />

        <h3
          class="text-center text-2xl font-bold mb-9 text-primary flex justify-center items-center"
        >
          Login
        </h3>
        <div class="form">
          <div class="form-container">
            <validation-observer v-slot="{ invalid }">
              <div class="form-inputs">
                <validation-provider
                  name="name"
                  rules="required|min:3|max:80"
                  v-slot="v"
                >
                  <div class="group">
                    <input
                      class="inputMaterial"
                      type="email"
                      name="email"
                      v-model="user.email"
                      placeholder="Email"
                    />
                    <label for="email">Email</label>
                    <div class="text-red-500 flex mx-auto h-2">
                      {{ v.errors[0] }}
                    </div>
                  </div>
                </validation-provider>
                <validation-provider
                  name="email"
                  rules="required|min:3|max:80"
                  v-slot="v"
                >
                  <div class="group">
                    <input
                      class="inputMaterial"
                      type="password"
                      name="password"
                      v-model="user.password"
                      placeholder="Password"
                    />
                    <label for="email">Password</label>
                    <div class="text-red-500 flex mx-auto h-2">
                      {{ v.errors[0] }}
                    </div>
                  </div>
                </validation-provider>
                <a
                  class="text-primary text-sm font-bold block"
                  :class="$i18n.locale === 'en' ? 'text-right' : 'text-left'"
                  href="/forgetPassword"
                  >Forget Password</a
                >
              </div>
              <div class="flex justify-center">
                <button
                  :class="{ 'cursor-default ': invalid }"
                  @click="submit()"
                  :disabled="invalid"
                >
                  Login
                </button>
              </div>
            </validation-observer>
          </div>
        </div>
        <div class="flex justify-center mt-4">
          <a class="text-center text-primary font-bold text-sm" href="/register"
            >I don't have an account?</a
          >
        </div>
      </div>
    </template>
  </section>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      loading: false,
      loggedIn: false,
      user: {
        email: "",
        password: "",
      },
    };
  },

  methods: {
    submit() {
      if (this.loggedIn) {
        return;
      }
      this.loading = true;
      this.axios
        .post("", this.user)
        .then((data) => {
          this.loggedIn = true;
          console.log(data);
        })
        .catch((errors) => {
          if (
            errors.response &&
            errors.response.data &&
            errors.response.data.errors
          ) {
            console.log(errors.response.data.errors);
            this.$refs.loginForm.setErrors(errors.response.data.errors);
          }
        })
        .finally(() => (this.loading = false));
    },
  },
};
</script>
<style scoped lang="scss">
.contact-us {
  min-height: calc(100vh - 420px);
}
</style>
